<template>
  <div class="section">
    <page-content :contentId="contentId">

      <template slot="beforeContent">
        <p>
          Hi {{ personName }} you have a ticket but we just need a few more details.
        </p>
        <p>Please fill in the form below</p>
      </template>

    </page-content>

    <ticket-offer-form @submitForm="proceedToTicket" />
  </div>
</template>

<script>
import PageContent from "@/ticketing/components/PageContent";
import TicketOfferForm from "@/ticketing/views/TicketOfferForm";
import Markdown from "@/components/Markdown";
import CopyTicketOffer from "@/ticketing/components/CopyTicketOffer";
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";
import { mapGetters } from "vuex";

export default {
  name: "TicketOfferPending",
  components: {
    PageContent,
    Markdown,
    CopyTicketOffer,
    TicketOfferForm
  },
  mixins: [personNameMixin, markdownSubstitutionsMixin],
  data() {
    return {
      contentName: "ticket_offer_page_content"
    }
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    }),
    contentId() {
      return this.event.pageContent.find( c => c[0] === this.contentName)[1]
    },
    applicationQuestions() {
      return this.person.data;
    },
    ticketQuestions() {
      return this.person.data;
    },
    pageTitle() {
      return this.substituteContent(
        this.event.pageTicketOfferTitle,
        this.person,
        this.event
      );
    },
    pageSubTitle() {
      return this.substituteContent(
        this.event.pageTicketOfferSubtitle,
        this.person,
        this.event
      );
    },
    pageContent() {
      return this.substituteContent(
        this.event.pageTicketOfferContentMarkdown,
        this.person,
        this.event
      );
    }
  },

  methods: {
    proceedToTicket() {
      ProfileService.updateProfile(this.person.id, {
        person: {
          firstName: this.person.firstName,
          lastName: this.person.lastName,
          email: this.person.email
        },
        profile: {
          ...this.applicationQuestions,
          ...this.ticketQuestions
        }
      })
        .then(() => {
          this.$emit("recalcNeeded");
          NProgress.done();
        })
        .catch(e => {
          NProgress.done();
          console.log("ERROR", e);
        });
    }
  }
};
</script>

<style>
.link {
  text-decoration: underline;
}
</style>
